// @Tags api
// @Summary 
// @Security ApiKeyAuth
// @accept application/json
// @Produce application/json
// @Param data body modelInterface.PageInfo true ""
// @Success 200 {string} json "{"success":true,"data":{},"msg":""}"
// @Router /api/getApiList [post]
// {
//  page     int
//  pageSize int
// }
export const getApiList = (data) => {
  return service({
    url: '/api/getApiList',
    method: 'post',
    data
  })
}

