<template>
  <div>
    <div class="button-box clearflex">
      <el-button @click="addMenu('0')" type="primary"></el-button>
    </div>

    <!--  pageSize999 -->
    <el-table :data="tableData" border stripe row-key="ID">
      <el-table-column label="ID" min-width="100" prop="ID"></el-table-column>
      <el-table-column label="Name" min-width="160" prop="name"></el-table-column>
      <el-table-column label="" min-width="80" prop="hidden">
        <template slot-scope="scope">
          <span>{{scope.row.hidden?"":""}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="70" prop="parentId"></el-table-column>
      <el-table-column label="" min-width="70" prop="sort"></el-table-column>
      <el-table-column label="" min-width="400" prop="component"></el-table-column>
      <el-table-column label="" min-width="120" prop="authorityName">
        <template slot-scope="scope">
          <span>{{scope.row.meta.title}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="140" prop="authorityName">
        <template slot-scope="scope">
          <span>{{scope.row.meta.icon}}</span>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="" width="300">
        <template slot-scope="scope">
          <el-button @click="deleteMenu(scope.row.ID)" size="small" type="text"></el-button>
          <el-button @click="editMenu(scope.row.ID)" size="small" type="text"></el-button>
          <el-button @click="addMenu(scope.row.ID)" size="small" type="text"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :before-close="handleClose" :visible.sync="dialogFormVisible" title="">
      <el-form :inline="true" :model="form" label-width="80px">
        <el-form-item label="name">
          <el-input autocomplete="off" placeholder="" v-model="form.path"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select placeholder="" v-model="form.hidden">
            <el-option :value="false" label=""></el-option>
            <el-option :value="true" label=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Id">
          <el-input autocomplete="off" disabled v-model="form.parentId"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input autocomplete="off" v-model="form.component"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input autocomplete="off" v-model="form.meta.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input autocomplete="off" v-model="form.meta.icon"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input autocomplete="off" v-model="form.sort"></el-input>
        </el-form-item>
      </el-form>
      <div class="dialog-footer" slot="footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button @click="enterDialog" type="primary"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// mixins  getTableData 

import {
  updataBaseMenu,
  getMenuList,
  addBaseMenu,
  deleteBaseMenu,
  getBaseMenuById
} from '@/api/menu'
import infoList from '@/components/mixins/infoList'
export default {
  name: 'Menus',
  mixins: [infoList],
  data () {
    return {
      listApi: getMenuList,
      listKey: 'list',
      dialogFormVisible: false,
      form: {
        ID: 0,
        path: '',
        name: '',
        hidden: '',
        parentId: '',
        component: '',
        meta: {
          title: '',
          icon: ''
        }
      },
      isEdit: false
    }
  },
  methods: {
    handleClose (done) {
      this.initForm()
      done()
    },
    // 
    load (tree, treeNode, resolve) {
      resolve([
        {
          id: 31,
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }, {
          id: 32,
          date: '2016-05-01',
          name: '',
          address: ' 1519 '
        }
      ])
    },
    // 
    deleteMenu (ID) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await deleteBaseMenu({ ID })
        if (res.success) {
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getTableData()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    initForm () {
      this.form = {
        path: '',
        name: '',
        hidden: '',
        parentId: '',
        component: '',
        meta: {
          title: '',
          icon: ''
        }
      }
    },
    // 
    closeDialog () {
      this.dialogFormVisible = false
    },
    // menu
    async enterDialog () {
      let res
      this.form.name = this.form.path
      if (this.isEdit) {
        res = await updataBaseMenu(this.form)
      } else {
        res = await addBaseMenu(this.form)
      }
      if (res.success) {
        this.$message({
          type: 'success',
          message: '!'
        })
        this.getTableData()
      } else {
        this.$message({
          type: 'error',
          message: '!'
        })
      }
      this.dialogFormVisible = false
    },
    // id 0
    addMenu (id) {
      this.form.parentId = String(id)
      this.isEdit = false
      this.dialogFormVisible = true
    },
    // 
    async editMenu (id) {
      const res = await getBaseMenuById({ id })
      this.form = res.data.menu
      this.dialogFormVisible = true
      this.isEdit = true
    }
  },
  created () {
    this.pageSize = 999
  }
}
</script>
<style scoped lang="scss">
.button-box {
  padding: 10px 20px;
  .el-button {
    float: right;
  }
}
</style>
