      const res = await regist(this.userInfo)
      if (res.success) {
        this.$message({ type: 'success', message: '' })
      }
      await this.getTableData()
      this.closeAddUserDialog()
    },
    closeAddUserDialog () {
      this.userInfo = {
        username: '',
        password: '',
        nickName: '',
        headerImg: '',
        authorityId: ''
      }
      this.addUserDialog = false
    },
    handleAvatarSuccess (res) {
      this.userInfo.headerImg = res.data.file.url
    },
    addUser () {
      this.addUserDialog = true
    },
    async changeAuthority (row) {
      const res = await setUserAuthority({
        uuid: row.uuid,
        authorityId: row.authority.authorityId
      })
      if (res.success) {
        this.$message({ type: 'success', message: '' })
      }
    },
    handleChange (value) {
      console.log(value)
    },
    getAuthOptions (list) {
      if (list && list.length > 0) {
        list.forEach(l => {
          let obj = l
          if (obj.children.length === 0) {
            delete obj['children']
            this.authOptions.push(obj)
          } else {
            let children = obj.children
            delete obj['children']
            this.authOptions.push(obj)
            this.getAuthOptions(children)
          }
        })
      }
    }
  },
  async created () {
    const res = await getAuthorityList({ page: 1, pageSize: 999 })
    let list = res.data.list
    this.authOptions = []
    this.getAuthOptions(list)
    console.log(this.authOptions)
