let qiniu = require('qiniu-js')

/** base64
 *  @param {String} base64 base64
 * */
export const convertBase64ToBlob = (base64) => {
  var base64Arr = base64.split(',')
  var imgtype = ''
  var base64String = ''
  if (base64Arr.length > 1) {
    // base64
    base64String = base64Arr[1]
    imgtype = base64Arr[0].substring(base64Arr[0].indexOf(':') + 1, base64Arr[0].indexOf(';'))
  }
  // base64
  var bytes = atob(base64String)
  // var bytes = base64;
  var bytesCode = new ArrayBuffer(bytes.length)
  // 
  var byteArray = new Uint8Array(bytesCode)

  // base64ascii
  for (var i = 0; i < bytes.length; i++) {
    byteArray[i] = bytes.charCodeAt(i)
  }

  // Blob
  /* eslint-disable */
  return new Blob( [bytesCode], {type : imgtype})
  /* eslint-enable */
}

export const uploadQiniu = (file, token, key) => {
  return new Promise((resolve, reject) => {
    let finishedAttr = []
    let compareChunks = []

    let config = {
      useCdnDomain: true,
      disableStatisticsReport: false,
      retryCount: 6,
      region: qiniu.region.z0
    }

    let putExtra = {
      fname: '',
      params: {},
      mimeType: ['audio/mp3', 'audio/mpeg', 'video/mp4', 'application/vnd.openxmlformats-officedocument.presentationml.presentation', 'application/vnd.ms-powerpoint', 'image/jpeg', 'image/png', 'image/webp', 'application/pdf'] || null
    }

    let observable = qiniu.upload(file, key, token, putExtra, config)

    observable.subscribe({
      next: (response) => {
        console.log(response)
        var chunks = response.chunks || []
        var total = response.total
        // chunk
        for (var i = 0; i < chunks.length; i++) {
          /* eslint-disable */
          if (chunks[i].percent === 0 || finishedAttr[i]) {
            continue;
          }
          if (compareChunks[i].percent === chunks[i].percent) {
            continue;
          }
          /* eslint-enable */
          if (chunks[i].percent === 100) {
            finishedAttr[i] = true
          }
          console.log(chunks[i].percent + '%')
        }
        console.log('' + total.percent + '%')
        compareChunks = chunks
      },
      error: (err) => {
        reject(err)
      },
      complete: (res) => {
        resolve(res)
      }
    })
  }, error => {
    // 
    this.$message({
      showClose: true,
      message: '~',
      type: 'error'
    })
    console.log(error)
  })
}
