<template>
  <div class="authority">
    <div class="button-box clearflex">
      <el-button @click="addAuthority('0')" type="primary"></el-button>
    </div>
    <el-table
            :data="tableData"
            style="width: 100%"
            row-key="ID"
            border
            stripe
            :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
        <el-table-column label="id" min-width="180" prop="ID"></el-table-column>
        <el-table-column label="id" min-width="180" prop="authorityId"></el-table-column>
        <el-table-column label="" min-width="180" prop="authorityName"></el-table-column>
        <el-table-column fixed="right" label="" width="500">
            <template slot-scope="scope">
                <el-button @click="opdendrawer(scope.row)" size="small" type="text"></el-button>
                <el-button @click="deleteAuth(scope.row)" size="small" type="text"></el-button>
                <el-button @click="addAuthority(scope.row.authorityId)" size="small" type="text"></el-button>
            </template>
        </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog :visible.sync="dialogFormVisible" title="">
      <el-form ref="form" :model="form">
         <el-form-item label="ID">
          <el-input autocomplete="off" disabled v-model="form.parentId"></el-input>
        </el-form-item>
        <el-form-item label="ID" prop="authorityId" :rules="[
            {required: true, message: 'ID', trigger: 'blur'},
            {pattern: /^(?!0+$)^\d*$/, message: '0'}
          ]">
          <el-input autocomplete="off" v-model="form.authorityId"></el-input>
        </el-form-item>
        <el-form-item label="" prop="authorityName" :rules="[
            {required: true, message: '', trigger: 'blur'},
          ]">
          <el-input autocomplete="off" v-model="form.authorityName"></el-input>
        </el-form-item>
      </el-form>
      <div class="dialog-footer" slot="footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button @click="enterDialog" type="primary"> </el-button>
      </div>
    </el-dialog>

    <el-drawer :visible.sync="drawer" v-if="drawer" :with-header="false" size="40%" title="">
      <el-tabs class="role-box" type="border-card">
        <el-tab-pane label="">
          <Menus :row="activeRow" />
        </el-tab-pane>
        <el-tab-pane label="api">
          <apis :row="activeRow" />
        </el-tab-pane>
      </el-tabs>
    </el-drawer>
  </div>
</template>

<script>
// mixins  getTableData 
import {
  getAuthorityList,
  deleteAuthority,
  createAuthority
} from '@/api/authority'

import Menus from '@/views/superAdmin/authority/components/menus'
import Apis from '@/views/superAdmin/authority/components/apis'

import infoList from '@/components/mixins/infoList'
export default {
  name: 'Authority',
  mixins: [infoList],
  data () {
    return {
      listApi: getAuthorityList,
      listKey: 'list',
      drawer: false,
      activeRow: {},
      activeUserId: 0,
      dialogFormVisible: false,
      apiDialogFlag: false,
      form: {
        authorityId: '',
        authorityName: '',
        parentId: '0'
      }
    }
  },
  components: {
    Menus,
    Apis
  },
  methods: {
    opdendrawer (row) {
      this.drawer = true
      this.activeRow = row
    },
    // 
    deleteAuth (row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const res = await deleteAuthority({ authorityId: row.authorityId })
        if (res.success) {
          this.$message({
            type: 'success',
            message: '!'
          })
          this.getTableData()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    initForm () {
      for (const key in this.form) {
        this.form[key] = ''
      }
    },
    // 
    closeDialog () {
      this.initForm()
      this.dialogFormVisible = false
      this.apiDialogFlag = false
    },
    // 

    async enterDialog () {
      const res = await createAuthority(this.form)
      if (res.success) {
        this.$message({
          type: 'success',
          message: '!'
        })
        this.getTableData()
        this.closeDialog()
      }
      this.initForm()
      this.dialogFormVisible = false
    },
    // 
    addAuthority (parentId) {
      this.form.parentId = parentId
      this.dialogFormVisible = true
    }
  },
  created () {
    this.pageSize = 999
  }
}
</script>

<style lang="scss">
.authority {
  .button-box {
    padding: 10px 20px;
    .el-button {
      float: right;
    }
  }
}
.role-box {
    .el-tabs__content {
      height: calc(100vh - 150px);
      overflow: auto;
    }
  }
</style>
